<template>
  <div style="width:100%;height:100%">
    <h1 class="chart-top-title">药品生产管理</h1>
    <div class="chart-wrapper" ref="chart" style="height:324px;width:100%"></div>
    <p class="remarkes">备注：2012年～2017年全国药品生产企业销售额和进货额对比分析（千万）</p>
  </div>
</template>

<script>
import { vueBus } from '../../assets/js/common/vueBus'
export default {
  name: 'barDoubleChart',
  props: {
    echartsData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      anima:'',
      color: this.$chartColor || ['#08A7DF', '#8ABE32'],
      dataArr: [
        [111744.32, 130357.83, 150214.08, 166132.7, 183925.77, 200162.54],
        [117610.89, 137201.62, 158100.32, 174854.67, 193581.88, 221179.61]
      ],
      xData: ['2012','2013', '2014', '2015', '2016', '2017'],
      axisLabelColor: '#ccd6e0  ',
      axisLineColor: '#809ab1',
      seriesName: ['销售额', '生产额']
    }
  },
  watch: {
    echartsData: {
      handler: function(val, oldVal) {
        this.initChart()
      },
      deep: true //增加deep 观察对象的子对象变化
    },
     anima: {
      handler: function(val, oldVal) {
        if (val == 'animateStart') {
          this.myChart = this.$echarts.init(this.$refs.chart)
          this.initChart()
        }
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      let me = this
      vueBus.$on('imgOnload', v => {
        me.anima = v
      })
    })
  },
  methods: {
    initChart() {
      let series = []
      this.dataArr.map((item, index) => {
        let colorItem = '';
        if (index === 0) {
          colorItem = new this.$echarts.graphic.LinearGradient(0, 1, 0, 0, [
            {
              offset: 1,
              color: '#13D3F1'
            },
            {
              offset: 0,
              color: this.color[index]
            }
          ])
        }else {
          colorItem = new this.$echarts.graphic.LinearGradient(0, 1, 0, 0, [
            {
              offset: 1,
              color: '#BEE061'
            },
            {
              offset: 0,
              color: this.color[index]
            }
          ])
        }
        series.push(
          {
            name: this.seriesName[index],
            type: 'bar',
            barWidth: '30%',
            data: item,
            itemStyle: {
              color: colorItem
            },
            animationDelay: function(idx) {
              return idx * 100
            },
            animationDuration:function(idx){
              return idx * 200
            }
          }
        )
      })
      let option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          top: 40,
          bottom: 30,
          left: 20,
          right: 50,
          containLabel: true
        },
        legend: {
          show: false
        },
        xAxis: {
          type: 'category',
          data: this.xData,
          axisLabel: {
            textStyle: {
              color: this.axisLabelColor
            }
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: this.axisLineColor
            }
          },
          splitLine: {
            show: true,
            lineStyle: {
              width: 1,
              color: this.axisLineColor
            }
          }
        },
        yAxis:
          {
            type: 'value',
            name: '单位：千万',
            nameTextStyle: {
              color: this.axisLabelColor,
              fontSize: 16
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: this.axisLineColor
              }
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                fontSize: 14
              },
              color: this.axisLabelColor
            },
            splitLine: {
              show: false
            }
          },
        series: series,
        animationEasing: 'elasticOut'
      }
      this.myChart.setOption(option)
      window.onresize = this.myChart.resize
      var currentIndex = -1
      let _this = this
      setInterval(function() {
        var dataLen = option.series[0].data.length

        // 取消之前高亮的图形
        _this.myChart.dispatchAction({
          type: 'downplay',
          seriesIndex: 0,
          dataIndex: currentIndex
        })
        currentIndex = (currentIndex + 1) % dataLen
        // 高亮当前图形
        _this.myChart.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          dataIndex: currentIndex
        })
        // 显示 tooltip
        _this.myChart.dispatchAction({
          type: 'showTip',
          seriesIndex: 0,
          dataIndex: currentIndex
        })
      }, 1000)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
</style>



